import React, { memo, useState, useEffect, useMemo } from "react";
import { CaretDownOutlined } from "@ant-design/icons";
import { DatePicker } from "antd-mobile";
import dayjs from "dayjs";
type IHeaderDate = {
  onOk: Function;
  defaultValue?: Date;
};

const HeaderDate = (props: IHeaderDate) => {
  const [timeValue, setTimeValue] = useState(undefined);
  const [visible, setVisible] = useState(false);

  const updateTimeValue = () => {
    if (props.defaultValue) {
      setTimeValue(props.defaultValue);
    } else {
      setTimeValue(dayjs().toDate());
    }
  };

  useEffect(() => {
    updateTimeValue();
  }, [props.defaultValue]);

  const onChange = (date: Date) => {
    setTimeValue(date);
  };
  const onOk = (date: Date) => {
    setTimeValue(date);
    props.onOk(dayjs(date).format("YYYY-MM"));
    setVisible(false);
  };
  return useMemo(
    () => (
      <DatePicker
        mode="month"
        format="YYYY-MM"
        title="请选择日期"
        value={timeValue}
        onChange={onChange}
        onOk={onOk}
        onDismiss={() => setVisible(false)}
        visible={visible}
      >
        <div className="heaher_datepicker" onClick={() => setVisible(true)}>
          {dayjs(timeValue).format("YYYY-MM")}
          <CaretDownOutlined />
        </div>
      </DatePicker>
    ),
    [timeValue, visible]
  );
};

export default HeaderDate;
