import React from "react";
import { observer, inject } from "mobx-react";
import { Icon, Picker } from "antd-mobile";
import F2 from "@antv/f2";
import { Tooltip } from "@antv/f2/lib/plugin/Tooltip";
import HeaderDate from "@/components/HeaderDate";
import classnames from "classnames";
import EasyNarBar from "@/components/EasyNarBar";
import dayjs from "dayjs";
import nomessage from "@/assets/imgs/nomessage.png";
require("./style.scss");
@inject("performStatisticsStore")
@observer
class PerformStatistics extends React.Component<IProps, GState> {
  chart: any;
  componentDidMount() {
    const {
      performStatisticsStore: { getTableData, getCharData }
    } = this.props;
    // this.initChart();
    getTableData();
    getCharData().then(() => {
      this.initChart();
    });
  }

  componentWillUnmount() {
    const {
      performStatisticsStore: { reset }
    } = this.props;
    reset();
  }

  initChart = () => {
    const {
      performStatisticsStore: { chartData }
    } = this.props;
    this.chart = new F2.Chart({
      id: "mountNode",
      pixelRatio: window.devicePixelRatio,
      plugins: Tooltip
    });
    const data = chartData;
    this.chart.source(data.reverse(), {
      value: {
        type: "linear",
        min: 0
      }
    });
    this.chart.coord({
      transposed: true
    });
    this.chart.axis("label", {
      line: F2.Global._defaultAxis.line,
      grid: null
    });
    this.chart.axis("value", {
      line: null,
      grid: F2.Global._defaultAxis.grid,
      label: {
        rotate: -Math.PI / 2,
        textAlign: "end",
        textBaseline: "middle"
      }
    });
    this.chart
      .interval()
      .position("label*value")
      .color("type")
      .adjust({
        type: "dodge",
        marginRatio: 1 / 32
      });
    this.chart.render();
  };
  onOK = (date: Date) => {
    const {
      performStatisticsStore: { onOK, getCharData }
    } = this.props;
    onOK(date);
    getCharData().then(res => {
      this.initChart();
    });
  };
  render() {
    const {
      history,
      performStatisticsStore: { table = [], chartData, month }
    } = this.props;
    return (
      <>
        <EasyNarBar
          mainContent={<HeaderDate defaultValue={dayjs(month).toDate()} onOk={this.onOK} />}
          leftContent={<Icon type="left" />}
          onLeftClick={() => {
            history.goBack();
          }}
        ></EasyNarBar>
        <div className="perform-statistics-title">履职完成情况</div>
        <div className="perform-statistics-thead">
          <div className="perform-statistics-th_1">排名</div>
          <div className="perform-statistics-th_2">所属机构</div>
          <div className="perform-statistics-th_3">履职完成率</div>
          <div className="perform-statistics-th_4">未完成数</div>
        </div>
        {table.length == 0 ? (
          <div className="dataEmpty">
            <img src={nomessage} style={{ width: "100px", height: "100px" }}></img>
          </div>
        ) : (
          ""
        )}
        {table.map((item: any, i: number) => (
          <div key={item.id} className="perform-statistics-tbody">
            <div className="perform-statistics-tb_1">{i + 1}</div>
            <div className="perform-statistics-tb_2">{item.id_JSON.name}</div>
            <div className="perform-statistics-tb_3">{item.completionRate1 ? item.completionRate1 : "0%"}</div>
            <div className="perform-statistics-tb_4">{item.incomplete ? item.incomplete : "0%"}</div>
          </div>
        ))}
        <div className={classnames("perform-statistics-title", "lyn", { messagenull: table.length == 0 })}>履职质量</div>

        <div className="chart-line-wrap">
          <canvas className={classnames("chart-line", { StatisticsNothing: chartData.length <= 0 })} id="mountNode"></canvas>
          {chartData.length == 0 ? (
            <div className="dataEmpty">
              <img src={nomessage} style={{ height: "100px" }}></img>
            </div>
          ) : (
            ""
          )}
        </div>
      </>
    );
  }
}

export default PerformStatistics;
